@import '../../themes/variables';

@mixin video-modal($display) {
  display: $display;
  background-color: $mid_night;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
}

.video-modal-showing {
  @include video-modal(block)
}

.video-modal-hidden {
  @include video-modal(none)
}

/* Modal Content */
.video-modal-content {
  position: relative;
  background-color: $mid_night;
  width: 90%;
  margin: 20px auto;
}

.video-modal-header {
  padding: 2px 16px;
  .modal-title {
    color: $cloud;
    font-size: 20px;
  }
}

.video-modal-body {
  padding: 10px 16px;
  video {
    border-radius: 5px;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19);
  }
}

/* The Close Button */
.video-close {
  float: right;
  font-size: 28px;
  font-weight: bold;
  padding-top: 10px;
  padding-bottom:10px;
}

.close:hover,
.close:focus {
  text-decoration: none;
  cursor: pointer;
}
