@import '../../themes/variables';

@mixin btn-background($bg) {
  background: $bg;
  &:hover {
    background-color: darken($bg, 10%);
    transition: all 0.3s ease;
  }
  &:active {
    background: darken($bg, 20%);
  }
  &:focus {
    outline: 0;
  }
  &:disabled {
    background: darken($bg, 80%);
  }
}

.btn {
  color: $white;
  display: inline-block;
  font-weight: 400;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  border: 1px solid transparent;
  padding: .775rem .75rem;
  font-size: 1rem;
  line-height: 1.5;
  border-radius: .25rem;
  cursor: pointer;

  .icon {
    display: inline;
    margin: 5px;
  }
}

.btn-primary {
  @include btn-background($wisteria);

}

.btn-light {
  @include btn-background($white);
  border-color: $silver;
  color: $semi_grey;
}

.btn-block {
  display: block;
  position: relative;
  width: 100%;
  .icon {
    position: absolute;
    top: 50%;
    left: 20px;
    transform: translate(-50%, -70%);
  }
}

