@import '../../../../themes/variables';

.search-item-wrapper {
  .thumb-wrapper {
    position: relative;
    cursor: pointer;
    img {
      border-radius: 5px;
      width: 100%;
      height: 200px;
      max-width: 600px;
      box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 10px 20px 0 rgba(0, 0, 0, 0.19);
    }
    .play-icon {
      position: absolute;
      left: 50%;
      margin-left: -30px;
      top: 50%;
      margin-top: -30px;
      height: 60px;
      width: 53px;
      padding-left: 7px;
      background: $white;
      opacity: 0.85;
      border-radius: 50%;

      display: -webkit-flex;
      display: flex;

      -webkit-flex: 1 0 auto;
      flex: 1 0 auto;

      -webkit-align-content: center;
      align-content: center;

      -webkit-justify-content: center;
      justify-content: center;

      align-items: center;
      -webkit-align-items: center;

      font-size: 35px;
    }
  }

  .title-wrapper {
    display: -webkit-box;
    font-size: 25px;
    margin-top: 10px;
    margin-bottom: 10px;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
    line-height: 25px;
    height: 25px;
    text-overflow: ellipsis;
  }

  .description-wrapper {
    font-size: 14px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
    line-height: 22px;
    height: 66px;
    color: $dove_grey;
  }

  .item-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 20px;
    font-size: 12px;
    color: $dove_grey;
  }
}
