@import '../../themes/variables';

.home-container {
  padding: 50px 40px 50px;

  .grid-wrapper {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    //grid-gap: 30px;
    margin-left: -30px;
    > * {
      margin-left: 30px;
      margin-bottom: 30px;
    }
  }

  .home-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 40px;
    align-items: center;
    .home-title {
      font-size: 50px;
      font-weight: 700;
      color: $grey;
    }

  }
}

