@import '../../themes/variables';

@mixin modal($display) {
  display: $display;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
}

.modal-showing {
  @include modal(block)
}

.modal-hidden {
  @include modal(none)
}

.modal-backdrop {
  background-color: rgba(48, 49, 48, 0.7);
  height: 100%;
  position: fixed;
  transition: all 1.3s;
  width: 100%;
}

/* Modal Content */
.modal-content {
  position: relative;
  background-color: $white;
  padding: 0;
  width: 60%;
  border-radius: 8px;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19);
  -webkit-animation-name: animate-top;
  -webkit-animation-duration: 0.4s;
  animation-name: animate-top;
  animation-duration: 0.4s;
  margin:100px auto;
}

/* Add Animation */
@-webkit-keyframes animate-top {
  from {top:-300px; opacity:0}
  to {top:0; opacity:1}
}

@keyframes animate-top {
  from {top:-300px; opacity:0}
  to {top:0; opacity:1}
}

.modal-header {
  padding: 2px 16px;
  .modal-title{
    color: $black;
    font-size: 20px;
  }
}

.modal-body {padding: 10px 16px;}

/* The Close Button */
.close {
  float: right;
  font-size: 28px;
  font-weight: bold;
  padding-top:10px
}

.close:hover,
.close:focus {
  text-decoration: none;
  cursor: pointer;
}
