@import '../../themes/variables';

.input-wrapper {
  position: relative;
  border: 1px solid $silver;
  padding: 8px;
  border-radius: .25rem;
  input{
    border: none;
    outline: 0;
    padding-top: 16px;
    font-size: 1rem;
    width: 100%;
  }
  label {
    position: absolute;
    color:$semi_grey;
    font-size: .85rem;
    .required-symbol{
      color: $carrot;
      font-size: 1rem;
    }
  }
}