@import '../../themes/variables';
@import '../../themes/ruler';

.search-container {
  padding: 50px 40px 50px;

  .header-title{
    font-size: 50px;
    color: $black;
    margin-top: 20px;
    margin-bottom: 20px;
  }

  .search-result{
    margin-bottom: 30px;
    font-size: 13px;
    font-weight: 600;
  }

  .search-box {
    margin-bottom: 30px;
    input{
      padding: 10px 0 10px;
      font-size: 20px;
    }
  }

  .grid-wrapper {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 30px;
    //margin-left: -30px;
    //> * {
    //  margin-left: 30px;
    //  margin-bottom: 30px;
    //}
    @include tablet{
      grid-template-columns: repeat(2, 1fr);
    }
    @include medium-mobile {
      grid-template-columns: repeat(1, 1fr);
    }
  }

  .grid-item {
    background: red;
  }

  .loading-wrapper {
    display: flex;
    margin-top: 50px;
    align-items: center;
    justify-content: center;
  }

  .back-link {
    text-decoration: none;
    color: $wisteria
  }
}